import { Component, OnInit, ViewChild, ViewEncapsulation  } from '@angular/core';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import { BlogService } from '../providers/blog.service';
import { NgForm, FormGroup, FormBuilder, Validators } from '@angular/forms';
@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit {
  @ViewChild('myFirstModal')
  modal: ModalComponent;
  encapsulation: ViewEncapsulation.None
  private data: any;
  private user: any = {};
  message:String;
  private userDetails: any;
  constructor(private blog: BlogService) {
    this.userDetails = {
      oldPassword: '',
      password: '',
      confirmPassword : ''
    };
   }

  ngOnInit() {
  }
  open(textContent:any) {
    this.message = textContent;
    this.modal.open('sm');
  }
  changePassword(validVal: NgForm){
    if(validVal.valid){
      let userData:any = JSON.parse(localStorage.getItem('userData'));
    this.blog.changePassword(userData.userId,this.userDetails)
          .then(
         data => {
           console.log(data);
           this.data = data;
           if (this.data.status != 500) {
             validVal.resetForm();
             // this.userData = {};
             this.open('Changed Successfully');
           } else {
             console.log()
             this.open(JSON.parse(this.data['_body']).error);
           }        
         });
    }
  }
}



