import { Component, OnInit, Pipe, PipeTransform } from '@angular/core';
import { BlogService } from '../providers/blog.service';
import { Router,ActivatedRoute } from '@angular/router';
import { DomSanitizer, SafeResourceUrl, SafeUrl} from '@angular/platform-browser';

@Pipe({ name: 'safeHtml'})
export class SafeHtmlPipe implements PipeTransform  {
  constructor(private sanitized: DomSanitizer) {}
  transform(value) {
    console.log(this.sanitized.bypassSecurityTrustHtml(value))
    return this.sanitized.bypassSecurityTrustHtml(value);
  }
}

@Component({
  selector: 'middleContent-component',
  templateUrl: './middle-content.component.html',
  styleUrls: ['./middle-content.component.css']
})
export class MiddleContentComponent implements OnInit {
  posts;
  id;
  data
  constructor(private blog: BlogService,private route: ActivatedRoute,public router: Router, private _sanitizer: DomSanitizer) {
    this.Getposts(); // added by Abid 
  }

  ngOnInit() {  }
  // for get posts
  Getposts() {
    this.blog.loadingStart();
    this.blog.getallposts(0,10)
      .then(data => {      
        this.blog.loadingEnd();
        this.posts = data;
      });
  }
  // for load more posts
loadmore(){
  var b = this.posts;
  this.blog.loadingStart();
  this.blog.getallposts(this.posts.length,10)
          .then(data => {
             this.blog.loadingEnd();
            var c:any = data;
            c.forEach(element => {
             (b.push(element));
            });
            // data.forEach(element => {
            //   this.filterData.push(element);
            // });
            console.log(this.posts);
              // infiniteScroll.complete();
  
          });
  }

  texttrim(text,value,id){
    return text.substr(0,value)+'...'+'  <br><a class="readmore" href="/singlepost/'+id+'">Read More</a>';
  }

  public sanitizeImage(image: string) {
    return this._sanitizer.bypassSecurityTrustStyle(`url(${image})`);
  }



}



