import { Component, OnInit } from '@angular/core';
import { BlogService } from '../providers/blog.service';
import { Router,ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-category-wise-posts',
  templateUrl: './category-wise-posts.component.html',
  styleUrls: ['./category-wise-posts.component.css']
})
export class CategoryWisePostsComponent implements OnInit {
  posts;
  catId;
  id;
  itemsPPage = 10;
  curPage = '1';
  constructor(private blog: BlogService,private route: ActivatedRoute,public router: Router) {
    this.curPage = route.params['_value']['page'];
    if(route.params){
      this.catId = route.params['_value']['categoryid'];
   }
    this.loadPost(this.catId);
  }

  ngOnInit() {
  }

  texttrim(text,value,id){
    return text.substr(0,value)+'...'+'  <a href="/singlepost/'+id+'">Read More</a>';
  }

  
   // for load posts
   loadPost(id){
     this.blog.loadingStart();
    this.blog.getPostbyCategoryId(id)
      .then(data => {      
        this.blog.loadingEnd();
        this.posts = data;
      });
  }
 // for pagination
  pagination(i,p){    
    return ((Number(this.curPage)- 1)*this.itemsPPage)+i+1;
    // window.location.reload();
  }

  changePage(event){
    this.router.navigate(['/postByCategory/'+this.catId+"/"+event]);
    this.curPage = event;
  }


}



