import { Component, OnInit, ViewChild, ViewEncapsulation  } from '@angular/core';
import { SideBarComponent } from '../shared/side-bar/side-bar.component';
import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import { BlogService } from '../providers/blog.service';
import { NgModel } from '@angular/forms';
import { NgForm, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import 'jquery';
import 'bootstrap';
@Component({
  selector: 'app-add-post',
  templateUrl: './add-post.component.html',
  styleUrls: ['./add-post.component.css']
})
export class AddPostComponent implements OnInit {

   @ViewChild('myFirstModal')
    modal: ModalComponent;
    encapsulation: ViewEncapsulation.None
  categories;
  f:NgForm;
  message:String;
  showDialog = false;
  posts:any = {};
  showLoading = false;
  ckeditorContent;
   private data;
   public showMe = false;
   public config = {toolbarGroups:[
         { name: 'clipboard',   groups: [ 'clipboard', 'undo' ] },
         { name: 'editing',     groups: [ 'find', 'selection', 'spellchecker' ] },
         { name: 'links' },
         { name: 'insert',    groups: ['Image']  },
         { name: 'forms' },
         { name: 'tools' },
         { name: 'document',    groups: [ 'mode', 'document', 'doctools' ] },
         { name: 'others' },
         '/',
         { name: 'basicstyles', groups: [ 'basicstyles', 'cleanup' ] },
         { name: 'paragraph',   groups: [ 'list', 'indent', 'blocks', 'align', 'bidi' ] },
         { name: 'styles' },
         { name: 'colors' },
         { name: 'about' }
     ],
     removeDialogTabs:'image:advanced;link:advanced'
   };
 
  constructor(private blog: BlogService) { 
    this.loadCategories();
    this.ckeditorContent = '<p>My HTML</p>';
  }

  // for modal close
  close() {
        this.modal.close();
    }
  // for modal open  
  open(textContent:any) {
      this.message = textContent;
      this.modal.open('sm');
  }

  ngOnInit() {
  }
// for form Reset
  restForm(validVal: NgForm) {
    validVal.resetForm();
    this.posts.description = "";
    this.open("Added post Successfully");
 }
  // for post submit
  submitPost(validVal: NgForm){
    if(validVal.valid && this.posts.imageLink != ''){
      var a = JSON.parse(localStorage.getItem('userData'));
      this.posts.user_id = a.userId;
     
    this.blog.addPost(this.posts)
        .then(
            data => {
          
              if(validVal.valid){
                this.restForm(validVal);
               }
              // this.reset();
            }, 
            err => {
              // Log errors if any
              console.log(err);
            });
    }else {
      if(this.posts.imageLink == '')
        this.open("Please Upload an Post image");
      else if(this.posts.description == "")
        this.open("Please add Description");
      else
        this.open("Please fill all fields")
    }
  }
  // for load categories
  loadCategories(){
    
    this.blog.getCategory()
        .then(
            data => {
              
              console.log(data);
              this.categories = data
            }, //Bind to view
            err => {
              // Log errors if any
              console.log(err);
            });
  }
  myfile:any;
  fileChange(fileInput: any) {
    this.showLoading = true;
    this.myfile = fileInput.target.files[0];
    //let fileList: FileList = event.target.files;
      this.blog.fileUpload(this.myfile)
      .then(data => {
        this.showLoading = false;
        //console.log(data);
        this.posts.imageLink = '';
        this.posts.imageLink = (data['files'][0].url);      
      }, //Bind to view
      err => {
        // Log errors if any
        console.log(err);
      });
  }
  
}



