import { Component, OnInit } from '@angular/core';
import { MiddleContentComponent } from '../middle-content/middle-content.component';
import { SideBarComponent } from '../shared/side-bar/side-bar.component';
import { OwlModule } from 'ng2-owl-carousel';
import { BlogService } from '../providers/blog.service';
import { Router,ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  public posts:any=[];
  // public images:any = [
  //   {image:'01.jpg'},
  //   {image:'01.jpg'},
  //   {image:'01.jpg'},
  //   {image:'01.jpg'},
  //   {image:'01.jpg'},
  //   {image:'01.jpg'},
  //   {image:'01.jpg'},
  //   {image:'01.jpg'},
  //   {image:'01.jpg'},
  //   {image:'01.jpg'}
  // ];
  constructor(private blog: BlogService,private route: ActivatedRoute,public router: Router) {
    this.Getposts();
   }

  ngOnInit() {
  }
  // for get posts
  Getposts() {
    this.blog.getallposts(0,8)
      .then(data => {      
        this.posts = data;
      });
  }

  texttrim(text,value,id){
    return text.substr(0,value)+'...'+'<br>  <a class="readmore" href="/singlepost/'+id+'">Read More</a>';
  }
}



