import { Component, OnInit, ViewChild, ViewEncapsulation  } from '@angular/core';
import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import { BlogService } from '../providers/blog.service';
import { NgModel } from '@angular/forms';
import { NgForm, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import 'jquery';
import 'bootstrap';


@Component({
  selector: 'app-addcategory',
  templateUrl: './addcategory.component.html',
  styleUrls: ['./addcategory.component.css']
})
export class AddcategoryComponent implements OnInit {
   @ViewChild('myFirstModal')
    modal: ModalComponent;
    encapsulation: ViewEncapsulation.None
  category:any={};
  message:String;
  constructor(private blog: BlogService) { }
 // for modal close
  close() {
    this.modal.close();
}
// for modal open
open(textContent:any) {
  this.message = textContent;
  this.modal.open('sm');
}
  ngOnInit() {
  }


// for form Reset
restForm(validVal: NgForm) {
  validVal.resetForm();
  // this.posts.description = "";
  this.open("Added Category Successfully");
}
  // for add category 
  addCategory(validVal: NgForm){
    if(validVal.valid){
    this.blog.AddCategory(this.category)
        .then(
            data => {
              if(validVal.valid){
                this.restForm(validVal);
               }
            }, 
            err => {
              // Log errors if any
              console.log(err);
            });
    }
  }

  
}



