import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers } from '@angular/http';
import 'rxjs/add/operator/map';
import { Component } from '@angular/core';

declare var jQuery: any;

@Injectable()
export class BlogService {
  data;
  options;

  baseURL = 'https://blogsterlnew.herokuapp.com/';
  constructor(public http: Http) {
    let headers = new Headers();
    headers.append('Content-Type', 'multipart/form-data');
    headers.append('Accept', 'application/json');
    this.options = new RequestOptions({ headers: headers });
  }

  // stopLoader 

  public loadingEnd() {
    jQuery('body').addClass('loaded');
  }

  public loadingStart() {
    jQuery('body').removeClass('loaded');
  }

  // for add post
  public addPost(data) {
    return new Promise(resolve => {
      this.http.post(this.baseURL + 'posts', data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }
  // for get post
  getPost() {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'posts')
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  getPostbyUserId(userId) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'postsbyuser/' + userId)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  getUser(userId) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'listUser/' + userId)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  setViews(post, uniqueCode) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'post/' + post + '/' + uniqueCode + '/tipViews')
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  setUniqueID(){
     return new Promise(resolve => {
      this.http.get(this.baseURL + 'user/uniqId')
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
    
  }

  getDelPostbyUserId(userId) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'trashedPosts/' + userId)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }


  // for get single post
  getPostSingle(idValue) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'posts/' + idValue)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for add category
  public AddCategory(data) {
    return new Promise(resolve => {
      this.http.post(this.baseURL + 'category', data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }


  public fileUploadBase64(fileVal) {
    var data: any = {
      imgbase64: fileVal
    }

    return new Promise(resolve => {
      this.http.post(this.baseURL + 'file/upload64/', data)
        .map(res => res.json())
        .subscribe(data => {
          // console.log(data)
          this.data = data;
          resolve(this.data);
        })
    });
  }

  // for get category
  getCategory() {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'category')
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for delete category
  deletecategory(id) {
    return new Promise(resolve => {
      this.http.delete(this.baseURL + 'category/' + id)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for delete post
  deletepost(id) {
    return new Promise(resolve => {
      this.http.delete(this.baseURL + 'posts/' + id)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // Untrash Post
  undeletepost(id) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'untrash/' + id)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // Trash Post
  trashPost(id) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'trash/' + id)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for get all posts
  getallposts(limit, skip) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'posts/' + skip + '/' + limit + '/')
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for edit post
  public editPost(idval, data) {
    return new Promise(resolve => {
      this.http.put(this.baseURL + 'posts/' + idval, data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for edit category
  public editCategory(idval, data) {
    return new Promise(resolve => {
      this.http.put(this.baseURL + 'category/' + idval, data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }
  // for get single category
  getSingleCategory(idval) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'category/' + idval)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        });
    });
  }

  // for get category
  getPostbyCategoryId(categoryid) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'postsbycategory/' + categoryid)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        });
    });
  }

  // for login
  public login(data) {
    return new Promise(resolve => {
      this.http.post(this.baseURL + 'login', data)
        .map(res => res.json())
        .subscribe(data => {
          console.log(data.header);
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for register
  public registerNew(data) {
    console.log("My Name is ABID");
    return new Promise(resolve => {
      this.http.post(this.baseURL + 'register', data)
        .map(res => res.json())
        .subscribe(data => {
          console.log(data);
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for edit user
  public editProfile(idval, data) {
    return new Promise(resolve => {
      this.http.post(this.baseURL + 'profileEdit/' + idval, data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }




  //for change password
  public changePassword(userId, data) {
    return new Promise(resolve => {
      this.http.post(this.baseURL + 'user/' + userId + '/change-password', data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }
  //for forget Password
  public forgetPassword(data) {
    return new Promise(resolve => {
      this.http.post(this.baseURL + 'user/forgot-password', data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }
  // for reset Password
  public resetPassword(token, data) {
    return new Promise(resolve => {
      this.http.post(this.baseURL + 'user/resetpassword?token=' + token, data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }
  // for user verification 
  public userVerify(tokenId) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'user/verify/' + tokenId)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }
  //for file upload
  fileUpload(file) {
    console.log(file);
    let headers = new Headers();
    let formData: FormData = new FormData();
    formData.append('content', file);
    return new Promise(resolve => {
      this.http.post('https://blogsterlnew.herokuapp.com/file/upload', formData, {
        headers: headers
      })
        .map(res => res.json())
        .subscribe(data => {
          // console.log(data)
          this.data = data;
          resolve(this.data);
        })
    });
  }

  // for comments
  public comment(postId, data) {
    return new Promise(resolve => {
      this.http.post('https://blogsterlnew.herokuapp.com/post/' + postId + '/comment', data)
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }
  // for get comments
  public getComments(postId) {
    return new Promise(resolve => {
      this.http.get('https://blogsterlnew.herokuapp.com/post/' + postId + '/getComments')
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }

  // for Search
  public search(str) {
    return new Promise(resolve => {
      this.http.get(this.baseURL + 'post/' + str + '/search')
        .map(res => res.json())
        .subscribe(data => {
          this.data = data;
          resolve(this.data);
        }, err => {
          resolve(err);
        });
    });
  }
}



