import { Component, OnInit, ViewChild, ViewEncapsulation  } from '@angular/core';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import { NgForm,FormGroup, FormBuilder, Validators } from '@angular/forms';
import { BlogService } from '../providers/blog.service';
import { Router,ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.css']
})
export class ResetPasswordComponent implements OnInit {
  @ViewChild('myFirstModal')
  modal: ModalComponent;
  encapsulation: ViewEncapsulation.None
  private data: any;
  private userData:any = {};
  message:String;
  private tokenId ="";
  constructor(private blog: BlogService,public router: Router,private route: ActivatedRoute) { 
    this.userData ={
      password:"",
      confirmPassword: ""
}

  if(route.params){
    this.tokenId = route.params['_value']['tokenId'];
    this.blog.userVerify(this.tokenId)
    .then(data => {    
      console.log(this.data + "Mahi");  
      this.data = data;
    });
  }
  }

  ngOnInit() {
  }
  open(textContent:any) {
    this.message = textContent;
    this.modal.open('sm');
  }
  resetPassword(validVal: NgForm){
    if(validVal.valid){
    this.blog.resetPassword(this.tokenId,this.userData)
    .then(
      data => {
      console.log(this.data)
      // this.userData ={};
          this.open('Your password is Reset successfully.');   
       });
    }
  }
}



