import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { OwlModule } from 'ng2-owl-carousel';
import { CKEditorModule, CKEditorComponent } from 'ng2-ckeditor';
import { NgxPaginationModule } from 'ngx-pagination'; // <-- import the module
import { Ng2Bs3ModalModule } from 'ng2-bs3-modal/ng2-bs3-modal';
import { Component, OnInit, Pipe, PipeTransform } from '@angular/core';
import {ImageCropperComponent} from 'ng2-img-cropper';
import { FacebookModule } from 'ngx-facebook';

import { AppComponent } from './app.component';
import { HeaderComponent } from './shared/header/header.component';
import { MiddleContentComponent } from './middle-content/middle-content.component';
import { SideBarComponent } from './shared/side-bar/side-bar.component';
import { FooterComponent } from './shared/footer/footer.component';
import { MainComponent } from './main/main.component';
import { PostFullComponent } from './post-full/post-full.component';
import { CategoryComponent } from './category/category.component';
import { ContactComponent } from './contact/contact.component';
import { AddPostComponent } from './add-post/add-post.component';
import { PostListComponent } from './post-list/post-list.component';
import { MyPostComponent } from './my-post/my-post.component';
import { MyDelPostComponent } from './my-del-post/my-del-post.component';
import { AddcategoryComponent } from './addcategory/addcategory.component';
import { ListcategoryComponent } from './listcategory/listcategory.component';
import { SinglepostComponent } from './singlepost/singlepost.component';
import { EditProfileComponent } from './edit-profile/edit-profile.component';
import { BlogService } from './providers/blog.service';
import { DialogComponent } from './dialog/dialog.component';
import { EditPostComponent } from './edit-post/edit-post.component';
import { EditCategoryComponent } from './edit-category/edit-category.component';
import { CategoryWisePostsComponent } from './category-wise-posts/category-wise-posts.component';
import { ForgetPasswordComponent } from './forget-password/forget-password.component';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { ResetPasswordComponent } from './reset-password/reset-password.component';
import { EmailVerificationComponent } from './email-verification/email-verification.component';
import { DomSanitizer, SafeResourceUrl, SafeUrl} from '@angular/platform-browser';
import { SearchPageComponent } from './search-page/search-page.component';

// routes to serve each page
const appRoutes: Routes = [
  { path: '', component: MainComponent },
  { path: 'full', component: PostFullComponent },
  { path: 'category', component: CategoryComponent },
  { path: 'contact', component: ContactComponent },
  { path: 'addPost', component: AddPostComponent },
  { path: 'postList/:id', component: PostListComponent },
  { path: 'myPost/:id', component: MyPostComponent },
  { path: 'myDelPost/:id', component: MyDelPostComponent },
  { path: 'addCategory', component: AddcategoryComponent },
  { path: 'listCategory/:id', component: ListcategoryComponent },
  { path: 'singlepost/:_id', component: SinglepostComponent },
  { path: 'EditProfile', component: EditProfileComponent },
  { path: 'editPost/:_id', component: EditPostComponent },
  { path: 'editCategory/:_id', component: EditCategoryComponent },
  { path: 'postByCategory/:categoryid/:page', component: CategoryWisePostsComponent },
  { path: 'forgetPassword', component: ForgetPasswordComponent },
  { path: 'changePassword', component: ChangePasswordComponent },
  { path: 'resetpassword/:tokenId', component: ResetPasswordComponent },
  { path: 'verify/:tokenId', component: EmailVerificationComponent },
  { path: 'SearchPage/:dataVal/:id', component: SearchPageComponent }
  // { path: 'hero/:id',      component: HeroDetailComponent },
  // {
  //   path: 'heroes',
  //   component: HeroListComponent,
  //   data: { title: 'Heroes List' }
  // },
  // { path: '',
  //   redirectTo: '/heroes',
  //   pathMatch: 'full'
  // },
  // { path: '**', component: PageNotFoundComponent }
];

@Pipe({ name: 'safeHtml'})
export class SafeHtmlPipe implements PipeTransform  {
  constructor(private sanitized: DomSanitizer) {}
  transform(value) {
    console.log(this.sanitized.bypassSecurityTrustHtml(value)["changingThisBreaksApplicationSecurity"]);
    return "url("+this.sanitized.bypassSecurityTrustHtml(value)["changingThisBreaksApplicationSecurity"]+")";
  }
}

@Pipe({ name: 'safeHtmlURL'})
export class safeHtmlURL implements PipeTransform  {
  constructor(private sanitized: DomSanitizer) {}
  transform(value) {
    // console.log(this.sanitized.bypassSecurityTrustHtml(value)["changingThisBreaksApplicationSecurity"]);
    return this.sanitized.bypassSecurityTrustHtml(value)["changingThisBreaksApplicationSecurity"];
  }
}


// Declared modules
@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    SafeHtmlPipe,
    safeHtmlURL,
    MiddleContentComponent,
    SideBarComponent,
    FooterComponent,
    MainComponent,
    PostFullComponent,
    CategoryComponent,
    ContactComponent,
    AddPostComponent,
    PostListComponent,
    MyPostComponent,
    MyDelPostComponent,
    AddcategoryComponent,
    ListcategoryComponent,
    SinglepostComponent,
    EditProfileComponent,
    DialogComponent,
    EditPostComponent,
    EditCategoryComponent,
    CategoryWisePostsComponent,
    ForgetPasswordComponent,
    ChangePasswordComponent,
    ResetPasswordComponent,
    EmailVerificationComponent,
    SearchPageComponent,
    ImageCropperComponent
  ],
  
  // Imported Modules
  imports: [
    BrowserModule,
    CKEditorModule,
    FormsModule,
    OwlModule,
    Ng2Bs3ModalModule,
    HttpModule,
    NgxPaginationModule,
    FacebookModule.forRoot(),
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // <-- debugging purposes only
    )
  ],
  // Providers
  providers: [BlogService],
  bootstrap: [AppComponent]
})
export class AppModule { }



