import { Component, OnInit, ViewChild, ViewEncapsulation  } from '@angular/core';
import { SideBarComponent } from '../shared/side-bar/side-bar.component';
import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import { BlogService } from '../providers/blog.service';
import { NgModel } from '@angular/forms';
import { NgForm, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import 'jquery';
import 'bootstrap';
@Component({
  selector: 'app-edit-post',
  templateUrl: './edit-post.component.html',
  styleUrls: ['./edit-post.component.css']
})
export class EditPostComponent implements OnInit {
  postId;
   @ViewChild('myFirstModal')
    modal: ModalComponent;
    encapsulation: ViewEncapsulation.None
  categories;
  f:NgForm;
  message:String;
  showDialog = false;
  posts:any = {};
  ckeditorContent;
   private data;
   public showMe = false;
   public config = {toolbarGroups:[
         { name: 'clipboard',   groups: [ 'clipboard', 'undo' ] },
         { name: 'editing',     groups: [ 'find', 'selection', 'spellchecker' ] },
         { name: 'links' },
         { name: 'insert',    groups: ['Image']  },
         { name: 'forms' },
         { name: 'tools' },
         { name: 'document',    groups: [ 'mode', 'document', 'doctools' ] },
         { name: 'others' },
         '/',
         { name: 'basicstyles', groups: [ 'basicstyles', 'cleanup' ] },
         { name: 'paragraph',   groups: [ 'list', 'indent', 'blocks', 'align', 'bidi' ] },
         { name: 'styles' },
         { name: 'colors' },
         { name: 'about' }
     ],
     removeDialogTabs:'image:advanced;link:advanced'
   };
 
  constructor(private blog: BlogService,private route: ActivatedRoute) { 
    if(route.params){
      this.postId = route.params['_value']['_id'];
   } 
    this.Getposts(this.postId);
    // this.loadEditPostDetails();
    this.loadCategories();
    this.ckeditorContent = '<p>My HTML</p>';
    
  }

  // for modal close
  close() {
        this.modal.close();
    }
  // for modal open  
  open(textContent:any) {
      this.message = textContent;
      this.modal.open('sm');
  }

  ngOnInit() {
  }

  // for load categories
  loadCategories(){
    this.blog.getCategory()
        .then(
            data => {
              console.log(data);
              this.categories = data
            }, //Bind to view
            err => {
              // Log errors if any
              console.log(err);
            });
  }
   // for get posts
   Getposts(postId) {
    this.blog.getPostSingle(this.postId)
      .then(data => {      
        this.posts = data;
        // this.posts.category = data._category.id;
      });
  }
  // for edit post
  EditPostDetails(val){
    // Get all comments
    if(val.valid){
    this.blog.editPost(this.postId,this.posts)
        .then(data => {
      // this.GetSingleCategory(this.cateId);
      this.open("Edited Post Successfully");
      }, err=> {
        this.open("Some issue occured");
      });
  }
    }
}



