import { Component, OnInit, ViewChild, ViewEncapsulation  } from '@angular/core';
import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import { ActivatedRoute } from '@angular/router';
import { BlogService } from '../../providers/blog.service';
import {Router, NavigationExtras} from '@angular/router';
import { NgForm, FormGroup, FormBuilder, Validators } from '@angular/forms';

declare var jQuery:any;

@Component({
  selector: 'header-component',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  @ViewChild('myFirstModal')
  modal: ModalComponent;
  encapsulation: ViewEncapsulation.None
  private userData: any = {};
  private data: any;
  private user: any = {};
  public userVal:any = {};
  message:String;
  
  constructor(private blog: BlogService,private route: ActivatedRoute,public router: Router) { 
    this.userData = {"email": "", "password": ""};
    // this.user= {
    //   firstName:'',
    //   lastName:'',
    //   email:'',
    //   password:'',
    //   passwordConfirm:'',
    // };
    
  }

  ngOnInit() {
  }
  errorText = "";
  login(validVal: NgForm) {    
    if(validVal.valid){
    this.blog.login(this.userData)
      .then(
        data => {
          var tempData = data;
          this.data = data;
          if (this.data.status == 200) {
            var userInfo = {              
                firstname: this.data.user.firstname, 
                lastname:this.data.user.lastname, 
                email: this.data.user.email,
                userId: this.data.user.id,
                mobile: this.data.user.mobile,
                state: this.data.user.state,
                country: this.data.user.country          
            };
            localStorage.setItem('userData', JSON.stringify(userInfo));
            validVal.resetForm();
            this.open('Login Successfully');
            jQuery("#myModal").modal('hide').on('hidden.bs.modal', jQuery('#myModal .close').trigger('click'));
            // var a:any = JSON.parse(localStorage.getItem('userData'));
          } 
          else {
            console.log(data);
            this.errorText = JSON.parse(this.data['_body']).error;
          }
          
        });
    } 
  }
  open(textContent:any) {
    this.message = textContent;
    this.modal.open('sm');
  }
   register(validVal: NgForm, userValue) {
     this.open("Register function is in Repair.");
    //  this.user.role ='VENDOR';
     let TempData = userValue;
    //  delete TempData.confirmPassword;
    // this.blog.registerNew(this.user)
    // .then(data => {
    //   console.log(this.user);
    //   //  this.user = {};
    //    validVal.resetForm();
    //    this.open("Registerd Successfully");
    //      jQuery("#myModal").modal('hide').on('hidden.bs.modal', jQuery('#myModal .close').trigger('click'));
    // });
   
  }
  forgetPassword(){
    jQuery("#myModal").modal('hide').on('hidden.bs.modal', jQuery('#myModal .close').trigger('click'));
  }
  submitButton(validVal: NgForm, userValue){
    console.log(validVal);
    if(validVal.valid){
     this.register(validVal, userValue);
    }
  }

  hideRole(){
    
        var a = localStorage.getItem('userData');
    
        a = JSON.parse(a);
        // var b =[];
        // b.push(a);
        if(a){
          return false;
        }
        else{
          return true;
        }
      }
      logout(){
        localStorage.removeItem('userData');
        this.router.navigate(['/']);
      }
}



