import { Component, OnInit, ViewChild, ViewEncapsulation  } from '@angular/core';
import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import { BlogService } from '../providers/blog.service';
import { NgModel } from '@angular/forms';
import { NgForm, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import 'jquery';
import 'bootstrap';

@Component({
  selector: 'app-edit-category',
  templateUrl: './edit-category.component.html',
  styleUrls: ['./edit-category.component.css']
})
export class EditCategoryComponent implements OnInit {
  cateId;
  data;
  categories;
   @ViewChild('myFirstModal')
    modal: ModalComponent;
    encapsulation: ViewEncapsulation.None
  category:any={};
  message:String;
  constructor(private blog: BlogService,private route: ActivatedRoute) { 
    if(route.params){
      this.cateId = route.params['_value']['_id'];
   } 
    this.GetSingleCategory(this.cateId);
    // this.loadEditCategoryDetails();
  }
 // for modal close
  close() {
    this.modal.close();
}
// for modal open
open(textContent:any) {
  this.message = textContent;
  this.modal.open('sm');
}
  ngOnInit() {
  }


// for get GetSingleCategory
GetSingleCategory(cateId) {
  this.blog.getSingleCategory(this.cateId)
    .then(data => {      
      this.category = data;
    });
}
// for load edit Category
loadEditCategoryDetails(val){
  // Get all comments
  if(val.valid){
  this.blog.editCategory(this.cateId,this.category)
      .then(data => {
      // this.GetSingleCategory(this.cateId);
      this.open("Edited Category Successfully");
      }, err=> {
        this.open("Some issue occured");
      });
}
}

  
}



