// This component is used to load and show each post
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BlogService } from '../providers/blog.service';

@Component({
  selector: 'app-singlepost',
  templateUrl: './singlepost.component.html',
  styleUrls: ['./singlepost.component.css']
})

// Single post Class
export class SinglepostComponent implements OnInit {
  postId;
  postData:any = { user: {}};
  comment: any = {};
  comments: any;
  post;
  uIdVal;
  userData:any = {
    facebook:'',
    google:'',
    instagram:'',
    twitter:''
  };
  public images: any = [
    { image: '01.jpg' },
    { image: '01.jpg' },
    { image: '01.jpg' },
    { image: '01.jpg' },
    { image: '01.jpg' },
    { image: '01.jpg' },
    { image: '01.jpg' },
    { image: '01.jpg' },
    { image: '01.jpg' },
    { image: '01.jpg' }
  ];

  // Initial Function
  constructor(private route: ActivatedRoute, private blog: BlogService) {

    if (route.params) {
      this.postId = route.params['_value']['_id'];
    }
    this.loadPost(this.postId);
    this.getComments(this.postData.id);

    var valueId:any = localStorage.getItem("uuid");

    if(valueId){
      this.addViews(this.postId,valueId);
    } else {
      this.blog.setUniqueID()
      .then(data => {
        console.log('test');
        this.uIdVal = data;
        localStorage.setItem("uuid",this.uIdVal.uuid);
        this.addViews(this.postId,this.uIdVal.uuid);
      });
    }

    
    
  }

  addViews(postid,uniquecode){
    this.blog.setViews(postid,uniquecode)
    .then(data => {
        this.blog.loadingEnd();
        console.log(data);
      });

  }

  ngOnInit() {
  }

  // loading All Posts
  loadPost(id) {
    this.blog.getPostSingle(id)
      .then(data => {
        this.blog.loadingEnd();
        this.postData = data;
        this.getUserData(this.postData._user);
      });
  }


  addComments(comment) {
    var a: any = localStorage.getItem("userData");
    var data = { commentText: comment, userId: a };
    this.blog.comment(this.postData.id, data).then(data => {
      this.comment.commentText = "";
      this.getComments(this.postData.id);
      console.log(data);
    });
  }
  getComments(postId) {
    this.blog.getComments(postId).then(data => {
      this.comments = data;
    });
  }



  getUserData(userId){
    this.blog.getUser(userId)
    .then(data => {
      this.userData = data[0];
    })
  }
  // Conversion to Server Date to Local Date
  changeDate(dateVal) {
    let b = new Date(dateVal).toDateString().split(" ");
    if (dateVal!) {
      return b[1] + " " + b[2] + " " + b[3];
    } else {
      return "Just Now";
    }
  }
}



