import { Component, OnInit } from '@angular/core';
import { BlogService } from '../providers/blog.service';
import { Router,ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-post-list',
  templateUrl: './post-list.component.html',
  styleUrls: ['./post-list.component.css']
})
export class PostListComponent implements OnInit {
  posts;
  id;
  itemsPPage = 10;
  curPage = '1';
  constructor(private blog: BlogService,private route: ActivatedRoute,public router: Router) {
    this.curPage = route.params['_value']['id'];
    this.Getposts(); // added by Abid 
  }

  ngOnInit() {
  }
 // for get posts
  Getposts() {
    this.blog.loadingStart();
    this.blog.getPost()
      .then(data => {      
         this.blog.loadingEnd();
        this.posts = data;
      });
  }
    
 // for pagination
  pagination(i,p){    
    return ((Number(this.curPage)- 1)*this.itemsPPage)+i+1;
  }

  changePage(event){
    this.router.navigate(['/postList/'+event]);
    this.curPage = event;
  }

  texttrim(text,value,id){
    return text.substr(0,value)+'...'+'  <br><a class="readmore" href="/singlepost/'+id+'">Read More</a>';
  }
}



