import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { BlogService } from '../providers/blog.service';
import { Router,ActivatedRoute } from '@angular/router';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import 'jquery';
import 'bootstrap';
@Component({
  selector: 'app-listcategory',
  templateUrl: './listcategory.component.html',
  styleUrls: ['./listcategory.component.css']
})
export class ListcategoryComponent implements OnInit {
  Categories;
  category;
  id;
  @ViewChild('myFirstModal')
  modal: ModalComponent;
  encapsulation: ViewEncapsulation.None
  message:String;
  
  constructor(private blog: BlogService,private route: ActivatedRoute,public router: Router) {
    this.Getcategories();
   }

   // for modal close
  close() {
    this.modal.close();
}
// for modal open
open(textContent:any) {
  this.message = textContent;
  this.modal.open('sm');
}
  ngOnInit() {
  }
  // get categories
  Getcategories() {
    this.blog.getCategory()
      .then(data => {      
        this.category = data;
      });
  }
  // delete category
  DelCategory(catId) {
    console.log(catId.id);
    this.blog.deletecategory(catId.id)
      .then(
        data => {
          this.open("Deleted Category Successfully");
          this.Getcategories();
        }, //Bind to view
        err => {
          // Log errors if any
          console.log(err);
        });
  }
 
}



