import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { Router,ActivatedRoute } from '@angular/router';
import { BlogService } from '../providers/blog.service';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import 'jquery';
import 'bootstrap';
@Component({
  selector: 'app-my-post',
  templateUrl: './my-del-post.component.html',
  styleUrls: ['./my-del-post.component.css']
})
export class MyDelPostComponent implements OnInit {
  @ViewChild('myFirstModal')
  modal: ModalComponent;
  encapsulation: ViewEncapsulation.None;
  message:String;
  categories;
  posts;
  itemsPPage = 10;
  curPage = '1';
  constructor(private blog: BlogService,private route: ActivatedRoute,public router: Router) { 
    this.loadCategories();
    this.getPost();
    this.curPage = route.params['_value']['id'];
  }
 // for modal close
  close() {
    this.modal.close();
}
// fpr modal open
open(textContent:any) {
  this.message = textContent;
  this.modal.open('sm');
}

ngOnInit() {
}
  // for load categories
  loadCategories(){
     this.blog.loadingStart();
    this.blog.getCategory()
        .then(
            data => {
              console.log(data);
              this.categories = data
            }, //Bind to view
            err => {
              // Log errors if any
              console.log(err);
            });
  }
  // for get posts
  getPost() {
    let userData:any = JSON.parse(localStorage.getItem('userData'));
    this.blog.getDelPostbyUserId(userData.userId)
      .then(data => {      
         this.blog.loadingEnd();
        this.posts = data;
      });
  }
 // for delete posts
  deletePost(postId){
    this.blog.deletepost(postId.id)
    .then(data =>{
      this.open("Deleted post Successfully");
      this.getPost();
    }
    )
  }

    UndeletePost(postId){
    this.blog.undeletepost(postId.id)
    .then(data =>{
      this.open("Republished post Successfully");
      this.getPost();
    }
    )
  }

  //for pagination
  pagination(i,p){    
    return ((Number(this.curPage)- 1)*this.itemsPPage)+i+1;
  }

  changePage(event){
    this.router.navigate(['/myPost/'+event]);
    this.curPage = event;
  }

    // For Category name
    getCategory(catVal) {
      var name;
      this.categories.forEach(function (element) {
        if (element.id == catVal) {
          name = element.name;
        }
      });
      return name;
    }
}



