// Controller page for Editing the Current user profile
 
// External Services
import { Component, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {FormsModule,ReactiveFormsModule} from '@angular/forms';
import { NgModel } from '@angular/forms';
import { NgForm, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { ModalComponent } from 'ng2-bs3-modal/ng2-bs3-modal';
import {ImageCropperComponent, CropperSettings, Bounds} from 'ng2-img-cropper';

// Internal Services
import { BlogService } from '../providers/blog.service';

// Component Builder
@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css']
})
export class EditProfileComponent implements OnInit {
  @ViewChild('myFirstModal')
    modal: ModalComponent;
    message:any;
    encapsulation: ViewEncapsulation.None 

@ViewChild('croppImage')
cropBox: ModalComponent;
 data1:any;
 profile:any = {};
 name:any;
 a:any;
   cropperSettings1:CropperSettings;
  croppedWidth:number;
  croppedHeight:number;
  
  @ViewChild('cropper', undefined) cropper:ImageCropperComponent;
  constructor(private blog: BlogService) { 
       this.a = JSON.parse(localStorage.getItem('userData'));
      //  this.profile = this.a;
       this.name = 'Angular2'
    this.cropperSettings1 = new CropperSettings();
    this.cropperSettings1.width = 200;
    this.cropperSettings1.height = 200;

    this.cropperSettings1.croppedWidth = 300;
    this.cropperSettings1.croppedHeight = 300;

    this.cropperSettings1.canvasWidth = 400;
    this.cropperSettings1.canvasHeight = 300;

    this.cropperSettings1.minWidth = 100;
    this.cropperSettings1.minHeight = 100;

    this.cropperSettings1.rounded = false;

    this.cropperSettings1.cropperDrawSettings.strokeColor = 'rgba(255,255,255,1)';
    this.cropperSettings1.cropperDrawSettings.strokeWidth = 2;

    this.data1 = {};
    this.getUserData();
  }
   

  getUserData() {
    this.blog.getUser(this.a.userId)
     .then(data => {
        this.blog.loadingEnd();
        this.profile = data[0];
        delete this.profile.password;
        delete this.profile.password;
         }, err=> {
        this.open("Some issue occured");
      });
  }

  ngOnInit() {
  }

  updateUser(f){
    this.blog.editProfile(this.a.userId,this.profile)
     .then(data => {
       this.blog.loadingEnd();
      // this.GetSingleCategory(this.cateId);
      this.open("Edited User Successfully");
        this.a = JSON.parse(localStorage.getItem('userData'));
       var userInfo = {              
                firstname: this.profile.firstname, 
                lastname:this.profile.lastname, 
                email: this.profile.email,
                userId: this.a.userId,
                mobile: this.profile.mobile,
                state: this.profile.state,
                country: this.profile.country
            };
            localStorage.setItem('userData', JSON.stringify(userInfo));
      }, err=> {
        this.open("Some issue occured");
      });
  }

  // for modal close
  close() {
        this.modal.close();
    }
  // for modal open  
  open(textContent:any) {
      this.message = textContent;
      this.modal.open('sm');
  }
  cropped(bounds:Bounds) {
    this.croppedHeight =bounds.bottom-bounds.top;
    this.croppedWidth = bounds.right-bounds.left;
  }


  fileChangeListener($event) {
    var image:any = new Image();
    var file:File = $event.target.files[0];
    var myReader:FileReader = new FileReader();
    var that = this;
    myReader.onloadend = function (loadEvent:any) {
        image.src = loadEvent.target.result;
        that.cropper.setImage(image);

    };
    myReader.readAsDataURL(file);
    }


   public uploadImageFile(fileVal){
      // this.modalBig.close();
      // this.showSpinner = true;
      this.blog.fileUploadBase64(fileVal)
      .then(data => {     
        this.myfile = data;
        this.profile.imageURL = this.myfile.imageURL;
        // this.profile.userDetails.image = (this.myfile.imageURL);
        // this.showLoading = false;    
        // this.showSpinner = false;
      }, //Bind to view
      err => {
        // Log errors if any
        console.log(err);
      });
   }

  myfile:any;
  cropImage() {
      this.cropBox.open('md');
  }
}



